
:root {
    --colour-white: hsl(210, 20%, 98%);
    --colour-black: hsl(0, 5%, 10%);
}


@media (prefers-color-scheme: dark) {
    :root {
        --colour-background: rgb(29,29,38);
        --colour-text: var(--colour-white);
    }
}
  
  /* variable light */
@media (prefers-color-scheme: light) {
    :root {
        --colour-background: var(--colour-white);
        --colour-text: var(--colour-black);
    }
}


/* Logo */
.gh-head-logo, a:visited.gh-head-logo {
    font-size: 2.5rem;
    text-decoration: none;
    color: var(--colour-text);
}

/* Ghost navbar */
.nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-align: center;
    display: flex;
    justify-content: space-around;
}

.nav li {
    display: inline;
    text-align: center;
}

/* Cards */
.gh-card {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.gh-card-title {
    margin-top: 0;
    
}

.gh-card-content {
    flex: 2;
    margin-bottom: 3rem;
}

img.gh-card-image {
    flex-basis: 250px;
    object-fit: cover;
    height: 10rem;
    width: 100%;
}

.gh-card-link {
    text-decoration: none
}

article.gh-article.post {
    margin-top: 6rem;
}

.author p {
    text-decoration: none;
}






figure.kg-card.kg-image-card figcaption, figure.kg-card.kg-gallery-card figcaption {
    padding-left: 2rem;
    margin-top: 1rem;
    text-align-last: end;
}


/* Post Images */
figure.kg-card.kg-gallery-card.kg-width-wide .kg-gallery-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

figure.kg-card.kg-gallery-card.kg-width-wide .kg-gallery-row .kg-gallery-image:not(:first-of-type) {
    margin: 0 0 0 .375em;
}

figure.kg-card.kg-gallery-card.kg-width-wide .kg-gallery-row .kg-gallery-image:first-of-type {
    margin: 0 .375em 0 0;
}

figure.kg-card.kg-gallery-card.kg-width-wide .kg-gallery-row .kg-gallery-image img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}



