/* Youtube */
figure.kg-card.kg-embed-card iframe {
    width: 100%;
    max-width: 80ch;
    height: calc(((100vw*9)/16)*.9);
    max-height: calc((80ch*8.5)/16);
}

figure.kg-card.kg-embed-card {
    margin: 3rem 0;
}

/* All figure */
figure + :not(figure), :not(figure) + figure {
    margin: 3rem 0 0 0;
}

figure + figure {
    margin: 1rem 0 0 0;
}


/* Bookmark Cards */

figure.kg-card.kg-bookmark-card a.kg-bookmark-container {
    text-decoration: none;
    display: flex;
}

figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 100%;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 20px;
}

figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-content .kg-bookmark-title {
    font-size: 1rem;
    line-height: 1.4em;
    font-weight: 600;
    color: var(--colour-text);
}

figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-content .kg-bookmark-description {
    display: -webkit-box;
    font-size: 1rem;
    line-height: 1.5em;
    margin-top: 3px;
    color: #626d79;
    font-weight: 400;
    max-height: 44px;
    overflow-y: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-content .kg-bookmark-metadata {
    display: flex;
    align-items: center;
    margin-top: 22px;
    width: 100%;
    color: #394047;
    font-size: 1rem;
    font-weight: 500;
}

figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-content .kg-bookmark-metadata .kg-bookmark-icon {
    width: 20px;
    height: 20px;
    margin-right: 6px;
}

figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-content .kg-bookmark-metadata .kg-bookmark-author {
    display: inline;
}


figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-thumbnail {
    position: relative;
    flex-grow: 1;
    min-width: 33%;
}

figure.kg-card.kg-bookmark-card a.kg-bookmark-container .kg-bookmark-thumbnail img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 0 4px 4px 0;
}

figure.kg-card.kg-image-card.kg-width-full {
    height: 60vh;
}

figure.kg-card.kg-image-card.kg-width-full img {
    position: absolute;
    left: 0;
    width: 100vw;
    height: 60vh;
    object-fit: cover;
}

/* Title Post */

.gh-header.gh-canvas.gh-title {
    text-align: center;
    margin: .85rem 0;
    font-variant-caps: small-caps;
}

.gh-header.gh-canvas.gh-title * {
    text-align: center;
    font-size: 1rem;
    font-weight: initial;
}

.gh-header.gh-canvas.gh-title h1 {
    margin: 0 0;
}


div.cover-title {
    display: inline-flex;
    /* position: relative; */
    cursor: pointer;
    width: 100vw;
    height: 70vh;
    position: absolute;
    left: 0;
    color: #fff;
}

div.cover-title header {
    display: inline-block;
    /* font-size: 4em; */
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 2;
}

div.cover-title .black-cover {
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    /* width: 100vw; */
    /* height: 70vh; */
    position: absolute;
}

div.cover-title img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    z-index: -2;
}

div.cover-title-container {
    height: 70vh;
}

header img.gh-author-image {
    width: 6rem;
    height: 6rem;
    margin: auto;
}

footer {
    margin: 6rem 0 0 0;
}

.gh-post-upgrade-cta {
    margin: 3rem 0 0 0;
}

.gh-head-actions a {
    padding-inline: 1rem;
    text-decoration: none;
}
.gh-head-actions {
    position: absolute;
    right: 0;
    top: 0;
    padding: 1rem;
}

.gh-head-actions svg {
    height: 1rem;
    width: 1rem;
}

a:focus {
    outline: 2px solid var(--ghost-accent-color);
}